import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import introduce from '../views/introduce.vue'
import about from '../views/introduceView/about.vue'
import install from '../views/introductionView/install.vue'
import started from '../views/introductionView/started.vue'
import buttonView from '../views/components/buttonView.vue'
import inputView from '../views/components/inputView.vue'
import switchView from '../views/components/switchView.vue'
import radioView from '../views/components/radioView.vue'
import checkboxView from '../views/components/checkboxView.vue'
import dialogView from '../views/components/dialogView.vue'
import page404 from '../views/page404.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/introduce',
    name: 'introduce',
    component: introduce,
    children: [
      { path: '/introduce/', component: about },
      {
        path: 'about',
        name: 'about',
        component: about
      },
      {
        path: 'install',
        name: 'install',
        component: install
      },
      {
        path: 'started',
        name: 'started',
        component: started
      },
      {
        path: 'button-view',
        name: 'buttonView',
        component: buttonView
      },
      {
        path: 'input-view',
        name: 'inputView',
        component: inputView
      },
      {
        path: 'switch-view',
        name: 'switchView',
        component: switchView
      },
      {
        path: 'radio-view',
        name: 'radioView',
        component: radioView
      },
      {
        path: 'checkbox-view',
        name: 'checkboxView',
        component: checkboxView
      },
      {
        path: 'dialog-view',
        name: 'dialogView',
        component: dialogView
      }
    ]
  },
  {
    path: '/page404',
    name: 'page404',
    component: page404
  },
  {
    path: '*',
    redirect: '/page404'
  }
]

const router = new VueRouter({
  // mode: 'history',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    } 
    else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})

export default router
